@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Kaushan Script);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
html {
	margin: 0 auto;
}

body {
	background: rgba(235, 235, 235);
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h2 {
	font-size: 32px;
	color: rgb(28, 168, 0);
	text-shadow: 2px 1px #000;
	padding: 5px 0;
}

p {
	font-size: 16px;
}

.pageContainer {
	font-family: 'Kaushan Script', cursive;
	font-weight: 300;
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.codeBorder {
	color: rgba(235, 235, 235);
	border: 1px solid black;
	background: #555;
	margin-top: 110px;
	padding: 5px 0;
	width: 100vw;
}

.aboutMe {
	width: 750px;
	padding: 20px;
}

.aboutMe span {
	font-size: 24px;
}

.port {
	margin-bottom: 150px;
}

.skills {
	background: #fff;
	border: 1px solid rgb(28, 168, 0);
	box-shadow: 2px 1px 5px #000;
	margin: 50px 0 150px 0;
	width: 1200px;
	height: 600px;
	padding: 20px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: relative;
}

.pageContainer img {
	width: 100%;
	height: 100vh;
	opacity: 0.1;
	position: absolute;
}

.portfolio {
	position: relative;
}
.portfolio img {
	opacity: 0.1;
	width: auto;
	height: 100%;
	position: absolute;
	left: 0;
}

.portfolio .portImg {
	opacity: 0.1;
	width: auto;
	height: 100%;
	position: absolute;
	width: 100vw;
}

.languages {
	height: 70px;
	background: rgba(235, 235, 235);
	border: 1px solid #555;
	width: 100%;
	display: flex;
	justify-content: space-around;
}

.icon {
	display: inline;
	margin: auto 25px;
}

.contact {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.topInput {
	margin: 0 20px;
	padding: 10px 5px;
	width: 300px;
	text-align: start;
}

.bottomInput {
	margin: 20px 0;
	padding: 10px 5px;
	width: 645px;
	text-align: start;
}

.messageInput {
	padding: 20px 10px;
	width: 645px;
	height: 200px;
	text-align: start;
}

button {
	background: rgb(28, 168, 0);
	border: 1px solid black;
	color: #fff;
	text-shadow: 1px 1px #000;
	padding: 10px 30px;
	margin: 30px;
}

button:hover {
	cursor: pointer;
	background: #fff;
	color: rgb(28, 168, 0);
}

.portContainer {
	background: #ffffff;
	border: 1px solid rgb(28, 168, 0);
	box-shadow: 2px 1px 5px #000;
	width: 1200px;
	padding: 20px;
	position: relative;
}

.backdrops {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin: 40px;
}

.backdrops h2 {
	display: flex;
	width: 100%;
	margin: 0 0 100px 0;
	padding: 0;
}

.backdrop {
	background: #fff;
	/* background-image: url('https://upload.wikimedia.org/wikipedia/commons/6/66/An_up-close_picture_of_a_curious_male_domestic_shorthair_tabby_cat.jpg'); */
	background-size: 100%;
	border: 1px solid #000;
	box-shadow: 2px 1px 5px #000;
	margin: 25px;
	padding: 120px 70px;
	position: relative;
	height: 150px;
	width: 30%;
}

.backdrop .projectImg {
	position: absolute;
	top: 0;
	opacity: 1;
}

.backdrop h3 {
	color: #555;
	text-shadow: 1px 1px rgba(235, 235, 235);
	padding: 3px 10px;
	display: inline;
	text-decoration: underline;
	position: relative;
}

.backdrop p {
	width: 100%;
	color: #fff;
	text-shadow: 1px 1px #000;
}

.backdrop a {
	background-color: rgb(28, 168, 0);
	border: 1px solid #000;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	margin: 15px;
	padding: 0 10px;
	text-shadow: 1px 1px #000;
	display: inline;
	position: relative;
}

.backdrop .description {
	background-color: #f0d91d;
	border: 1px solid #000;
	border-radius: 15px;
	padding: 10px 0;
	width: 220px;
	position: absolute;
	left: 25%;
}

.backdrop a:hover {
	background-color: gray;
	color: #fff;
}

#projectTools {
	background: #555;
	border-top: 1px solid #000;
	color: #fff;
	bottom: 0;
	width: 100%;
	display: flex;
	position: absolute;
	left: 0;
}

.container {
	text-align: center;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
	pointer-events: none;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

h1 {
	font-size: 24px;
	margin: 0 20px;
}

img {
	height: 80px;
	width: auto;
}

a {
	color: #000;
	font-size: 24px;
	text-decoration: none;
}

span {
	color: rgb(28, 168, 0);
	font-weight: 400;
}

.mainHeader {
	font-size: 32px;
	font-family: 'Cinzel', sans-serif;
	display: flex;
	justify-content: center;
	align-items: center;
	text-shadow: 1px 2px 3px #000;
}

.navbar {
	background: #ffffff;
	border-bottom: 1px solid #555;
	height: 100px;
	width: 100%;
	padding: 10px;
	z-index: 1;
	position: fixed;
	display: flex;
	justify-content: space-between;
}

.logo {
	margin-left: 25px;
}

.navItems {
	display: flex;
	align-content: center;
	align-self: center;
}

a.navItem {
	color: #000;
	font-size: 18px;
	padding: 5px 20px;
	margin: 0 15px;
	text-decoration: none;
}

a.navItem:hover {
	border: 1px solid black;
	color: rgb(28, 168, 0);
	text-shadow: 1px 1px #555;
	opacity: 0.5;
	box-shadow: 1px 1px #000;
}

